<template>
  <div class="module_div_header">
    <div class="module_div_search">
      <div class="module_div_operation">
        <el-button icon="my-xinzeng" @click="addModule">新增</el-button>
        <el-button icon="close" @click="disableSource">禁用</el-button>
        <el-button icon="check" @click="enableSource">启用</el-button>
        <!--<el-button @click="moduleSortDialogVisible=true">显示顺序调整</el-button>-->
      </div>

      <!--<el-row :gutter="10">-->
      <!--<el-col :span="24">-->
      <el-form :inline="true" :model="searchForm" class="module-div-form">
        <el-form-item label="名称">
          <el-input v-model="searchForm.form.name"></el-input>
        </el-form-item>
        <el-form-item label="url">
          <el-input v-model="searchForm.form.path"></el-input>
        </el-form-item>
        <el-form-item label="状态">
          <el-select v-model="searchForm.form.status">
            <el-option label="正常" :value="true"></el-option>
            <el-option label="禁用" :value="false"></el-option>
            <el-option label="全部" value=""></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="是否包含子模块">
          <el-select v-model="searchForm.hasChildren">
            <el-option label="包含" :value="true"></el-option>
            <el-option label="不包含" :value="false"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button class="search_button" @click="searchModule">查询</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div>
      <da-treeGrid ref="treeGrid" :columns="columns" :tree-structure="true" :data-source="dataSource" @selectionChange="disableModule($event)" :row-class-name="rowClassName" :showSelection="true" setTreeHeight="true">
        <el-table-column label="操作" width="195" align="left" header-align="center" slot="operation">
          <template slot-scope="scope">
            <el-button type="warning" @click="editModule(scope.row)" size="small">
              编辑
            </el-button>
            <el-button type="info" size="small" @click="sourceModuleId(scope.row)">
              资源
            </el-button>
            <el-button type="info" size="small" @click="groupAllGroup(scope.row)">
              资源分组
            </el-button>
          </template>
        </el-table-column>
      </da-treeGrid>
    </div>
    <!--增加dialog-->
    <da-dialog :title="title" :visible.sync="addVisible" size="tiny" class="module-div-addDialog" @close="closeDialog('moudle_add')">
      <el-form :model="form" label-width="80px" :rules="addRules" ref="moudle_add">
        <el-form-item label="模块名称" prop="name">
          <el-input v-model="form.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="上级模块">
          <da-select-tree :data="parentModuleTree" v-model="form.parentId" :defaultExpandAll="true"></da-select-tree>
        </el-form-item>
        <el-form-item label="访问地址" prop="path">
          <el-input v-model="form.path" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="默认高度" prop="showHeight">
          <el-input v-model.number="form.showHeight" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="默认宽度" prop="showWidth">
          <el-input v-model.number="form.showWidth" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="显示顺序" prop="showSort">
          <el-input v-model.number="form.showSort" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="大图标" prop="bigIcon">
          <el-input v-model="form.bigIcon" auto-complete="off" :maxlength="50"></el-input>
        </el-form-item>
        <el-form-item label="小图标" prop="smallIcon">
          <el-input v-model="form.smallIcon" auto-complete="off" :maxlength="50"></el-input>
        </el-form-item>
        <el-form-item label="状态">
          <el-radio-group v-model="form.status">
            <el-radio :label="true">正常</el-radio>
            <el-radio :label="false">禁用</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="显示方式">
          <el-select v-model="form.showType" placeholder="请选择活动区域">
            <el-option label="DIV方式弹窗（常规弹出窗口）" :value="0"></el-option>
            <el-option label="iframe方式弹窗（支持外部加载）" :value="1"></el-option>
            <el-option label="浏览器新选项卡（即在新页面中打开）" :value="2"></el-option>
            <el-option label="系统选项卡（默认）" :value="3"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="备注" prop="remark">
          <el-input v-model="form.remark" auto-complete="off" type="textarea" :rows="3"></el-input>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer ">
        <el-button type="primary" @click="saveAddDialog('moudle_add')">确 定</el-button>
        <el-button @click="cancelAddDialog">取 消</el-button>
      </div>
    </da-dialog>

    <!--资源dialog-->
    <da-dialog :title="sourceTitle" :visible.sync="sourceVisible" @open="openSource" class="module-div-sourceDialog" :close-on-click-modal="false" @close="closeSourceModule">
      <el-form :inline="true" :model="searchSourceForm" class="module-div-form">
        <el-form-item label="名称">
          <el-input v-model="searchSourceForm.name"></el-input>
        </el-form-item>
        <el-form-item label="备注">
          <el-input v-model="searchSourceForm.note"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button class="search_button" @click="searchSourceModule">查询</el-button>
        </el-form-item>
      </el-form>
      <da-transfer :chooseList="transfer.chooseList" :accredited="transfer.accredited" :filterList="query" filterType="1" :rend="rendContent" @transfer="handleRoleChange" :saveClick="saveClick" :titles="['未授权资源', '已授权资源']" mysortId="id" mysortName="note" :buttonTexts="['删除', '授权', '保存']">
      </da-transfer>
    </da-dialog>

    <!--功能dialog-->
    <da-dialog title="资源配置" :visible.sync="groupVisible" @open="openGroup" class="module-div-sourceDialog" :close-on-click-modal="false" @close="closeGroupModule">
      <!-- groupModuleLists -->
      <select-list :group-list="groupModuleLists" :detail-list="transfer.accredited" :module-id="moduleId" :init-check="groupInitCheck" v-model="groupSelect"></select-list>
      <span>
        <el-button type="button" @click="saveGroup">保存</el-button>
        <el-button type="button" @click="groupVisible=false;">关闭</el-button>
      </span>
    </da-dialog>
  </div>
</template>


<script>
import { changeTree, difference, deepCopy } from "@/util/util.js";
import treeSort from "@/components/base/treeSort";
import selectList from "@/components/base/selectList";

const addForm = {
  name: "",
  parentId: "",
  path: "",
  showHeight: "",
  showWidth: "",
  showSort: "",
  bigIcon: "",
  smallIcon: "",
  status: true,
  showType: 3,
  remark: ""
};
export default {
  name: "module",
  components: {
    treeSort,
    selectList
  },
  data() {
    return {
      //状态为全部对应的value
      columns: [
        {
          label: "名称",
          prop: "name",
          width: "200"
        },
        {
          label: "url",
          prop: "path",
          align: "left"
        },
        {
          label: "图标",
          prop: "smallIcon",
          align: "left"
        },
        {
          label: "打开方式",
          prop: "showType",
          displace: {
            0: "弹窗方式加载",
            1: "iframe方式加载",
            2: "浏览器新页面",
            3: "后台选项卡方式"
          }
        }
      ],
      dataSource: [],
      addVisible: false, //是否显示add弹框
      isAdd: "", //新增还是编辑
      searchForm: {
        form: {
          name: "",
          path: "",
          status: ""
        },
        hasChildren: true
      }, //查询发送的字段
      disableIds: [], //禁用模块对应的id
      moduleId: "", //资源对应的模块id
      editSourceTable: [], //资源弹窗打开的数据
      sourceTitle: "", //资源弹窗的标题

      //新增
      parentModule: [{ id: "0", name: "根模块", children: [] }], //[],//上级模块的options值
      form: JSON.stringify(addForm),
      parentModuleTree: [], //新增编辑模块下拉树数据
      addRules: this.eachValidate({
        name: { required: true, maxString: 20 },
        path: { maxString: 200 },
        bigIcon: { maxString: 50 },
        smallIcon: { maxString: 50 },
        remark: { maxString: 100 },
        showHeight: { max: 99999 },
        showWidth: { max: 99999 },
        showSort: { max: 99999999999 }
      }),
      props: {
        value: "id",
        label: "name",
        children: "children"
      },
      newArr: [],
      newParentId: "",

      //资源穿梭框
      sourceVisible: false, //显示资源列表弹框
      transfer: {
        chooseList: [], //总数据
        accredited: [] //右边的选项
      },
      list: {
        before: [], //之前保存的
        after: [] //之后保存的
      },
      searchSourceForm: {
        //表单搜索
        name: "",
        note: ""
      },
      query: {},
      index: 0, //记录打开资源框
      necessaryList: {
        before: [], //之前保存的
        after: [] //之后保存的
      },

      //显示顺序调整
      moduleSortDialogVisible: false,

      // 20180613 Willi
      groupVisible: false,
      groupModuleLists: [], // 功能列表
      groupInitCheck: {
        groupCheckedLists: [], // 模块-功能关联列表
        detailCheckedLists: [] // 功能-资源id关联对象
      },
      groupSelect: {
        handleSaveList: {
          handleList: [],
          resourceList: []
        },
        handleDeleteList: {
          handleList: [],
          resourceList: []
        }
      } // 双向绑定对象
    };
  },
  methods: {
    // searchModule() {
    //   let _this = this;
    //   _this
    //     .$ajax({
    //       url: "rest/module",
    //       method: "get",
    //       params: {
    //         formdata: {},
    //         hasChildren: ""
    //       }
    //     })
    //     .then(res => {
    //       if (res.data.status) {
    //         _this.dataSource = res.data.records;
    //       }
    //     })
    //     .catch(err => {});
    // },
    //add弹窗取消事件
    cancelAddDialog() {
      this.addVisible = false;
    },
    //add弹框保存事件
    saveAddDialog(formName) {
      //传出form信息,发送增加请求((该时间在内部处理)),请求成功后再发送一个全部信息的请求
      let _this = this;
      delete _this.form.parentName;
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (_this.isAdd) {
            //发送新增请求
            this.$ajax
              .post("rest/module", _this.form)
              .then(res => {
                if (res.data.status) {
                  this.$message.success(res.data.answerMsg);
                  this.searchModule();
                } else {
                  this.$message.error(res.data.answerMsg);
                }
                this.addVisible = false;
              })
              .catch(err => {});
          } else {
            //发送编辑请求
            this.$ajax({
              url: "rest/module",
              method: "PUT",
              data: _this.form
            }).then(res => {
              if (res.data.status) {
                _this.$message.success(res.data.answerMsg);
                this.searchModule();
              } else {
                this.$message.error(res.data.answerMsg);
              }
              this.addVisible = false;
            });
          }
        } else {
          return false;
        }
      });
    },
    //点击编辑传出
    editModule(val) {
      this.isAdd = false;
      let newV = {};
      //剔除表格树增加的属性
      for (var key in val) {
        if (key.indexOf("_") !== 0) {
          newV[key] = val[key];
        }
      }
      delete newV.children;
      this.findParentId(val, newV);
    },
    //查询模块
    searchModule() {
      this.$ajax
        .get("rest/module", {
          params: {
            formdata: JSON.stringify(this.searchForm.form),
            hasChildren: this.searchForm.hasChildren
          },
          loadingTarget: this.$refs.treeGrid
        })
        .then(res => {
          let nRes = res.data;
          if (nRes.status) {
            this.dataSource = nRes.records;
          } else {
            this.$message.error(nRes.msg);
          }
        })
        .catch(err => {});
    },
    //查找上级模块
    findParentId(val, formData) {
      let me = this;
      me
        .$ajax({
          url: "rest/module",
          method: "get",
          params: {
            formdata: {},
            hasChildren: ""
          }
        })
        .then(res => {
          if (res.data.status) {
            me.parentModuleTree = res.data.records;
            //编辑设值
            if (val != undefined) {
              //没有parentname时
              let parentTreeObj = {};
              changeTree(res.data.records, "id", "name", parentTreeObj);

              // this.$refs.parentModuleTree.setText(parentTreeObj[val.parentId]);
            }
          }
          this.addVisible = true;
          if (formData) {
            this.form = formData;
          }
        });
    },
    //新增模块
    addModule() {
      this.isAdd = true;
      this.form = JSON.parse(JSON.stringify(addForm));
      this.findParentId();
    },
    //禁用模块
    disableSource() {
      let me = this;
      this.$alertMsg(
        "确定禁用所选的模块吗？禁用后该模块功能将停止使用！",
        "操作确认",
        "warning",
        function() {
          if (me.disableIds.length == 0) {
            me.$message.warning("请选择资源");
          } else {
            let ids = me.disableIds.join(",");
            me
              .$ajax({
                url: "rest/module/state/disable",
                method: "put",
                data: ids
              })
              .then(res => {
                if (res.data.status) {
                  me.$message.warning("该模块及子模块功能已停止使用！");
                  me.searchModule();
                  me.disableIds = [];
                } else {
                  me.$message.error(res.data.answerMsg);
                }
              })
              .catch(err => {});
          }
        }
      );
    },
    //开启模块
    enableSource() {
      let me = this;
      if (me.disableIds.length == 0) {
        me.$message.warning("请选择资源");
      } else {
        let ids = me.disableIds.join(",");
        me
          .$ajax({
            url: "rest/module/state/enable",
            method: "PUT",
            data: ids
          })
          .then(res => {
            if (res.data.status) {
              me.$message.success(res.data.answerMsg);
              me.searchModule();
              me.disableIds = [];
            } else {
              me.$message.error(res.data.answerMsg);
            }
          });
      }
    },
    //传出禁用的id
    disableModule(val) {
      let arr = [];
      if (val && val.length > 0) {
        val.forEach(function(item) {
          arr.push(item.id);
        });
      }
      this.disableIds = JSON.parse(JSON.stringify(arr));
    },
    //监听点击table资源
    sourceModuleId(val) {
      this.moduleId = val.id;
      this.sourceVisible = true;
      this.sourceTitle = val.name + "模块资源配置";
    },
    // 获取所有功能
    groupAllGroup(row) {
      this.groupVisible = true;
      this.moduleId = row.id;
      this.$ajax.get("rest/module/allHandle").then(res => {
        this.groupModuleLists = res.data.records;
        this.getCheckedModule(row.id);
        this.getGroupModuleId(row.id);
        this.getCheckedGroup(row.id);
      });
    },
    openGroup() {},
    closeGroupModule() {},
    getCheckedModule(moduleId) {
      this.$ajax
        .get("rest/module/moduleResource/checked", {
          params: {
            moduleId
          }
        })
        .then(res => {
          this.transfer.accredited = res.data.records;

          this.list.before = this.transfer.accredited.map(item => item.id); //资源之前保存的id
          this.list.after = this.transfer.accredited.map(item => item.id); //资源之前保存的id
        });
    },
    // 功能-资源
    getCheckedGroup(moduleId) {
      this.$ajax
        .get("rest/module/moduleHandleResource", {
          params: { moduleId: moduleId }
        })
        .then(res => {
          this.groupInitCheck.detailCheckedLists = res.data.records;
        });
    },
    // 模块-功能
    getGroupModuleId(moduleId) {
      this.$ajax
        .get("rest/module/moduleHandle", { params: { moduleId } })
        .then(res => {
          this.groupInitCheck.groupCheckedLists = res.data.records;
        });
    },
    //打开资源dialog的回调
    // 保存功能模块信息
    saveGroup() {
      // return false;
      this.$ajax
        .post("rest/module/handleAndResource", this.groupSelect)
        .then(res => {
          if (res.data.status) {
            this.groupVisible = false;
          }
        });
    },
    openSource() {
      this.$nextTick(function() {
        this.$ajax
          .get("rest/module/baseResource/list")
          .then(res => {
            if (res.data.status) {
              this.transfer.chooseList = res.data.records;
            }
          })
          .catch()
          .then(() => {
            this.getCheckedModule(this.moduleId);
          })
          .then(() => {
            // setTimeout(() => {
            let inputs = this.$el
              .querySelectorAll(".el-transfer-panel__body")[1]
              .querySelectorAll(".transfer-accredited");
            this.transfer.accredited.forEach(item => {
              //找到之前的必须资源
              if (item.isNecessary) {
                this.necessaryList.before.push(item.id);
                [...inputs].forEach(node => {
                  if (node.value == item.id) {
                    node.checked = true;
                  }
                });
              }
            });
            // }, 200);
          });

        // if (!this.index) {
        //   this.index++;
        //   let span = document.createElement("span");
        //   span.innerHTML = "勾选是否必须资源！<strong>知道了</strong>   ";
        //   span.className = "module-source-isNecessary";
        //   this.$el
        //     .querySelectorAll(".el-transfer-panel__body")[1]
        //     .appendChild(span);
        //   span.querySelector("strong").addEventListener("click", function() {
        //     span.style.display = "none";
        //   });
        // }
      });
    },
    closeDialog(formName) {
      this.$refs[formName].resetFields();
      this.form = JSON.parse(JSON.stringify(addForm));
      //   this.$refs.parentModuleTree.setText("");
    },

    //资源
    //授权保存
    saveClick() {
      this.necessaryList.after = []; //清除后面的
      let me = this,
        list = difference(this.list.before, this.list.after); //获取保存之前的值和保存的值
      let inputs = this.$el
        .querySelectorAll(".el-transfer-panel__body")[1]
        .querySelectorAll(".transfer-accredited");
      [...inputs].forEach(item => {
        //获取必须资源选项
        if (item.checked) {
          this.necessaryList.after.push(item.value);
        }
      });
      let necessaryList = difference(
        this.necessaryList.before,
        this.necessaryList.after
      );
      //                necessaryList.saveList
      //                    updateList = getList(necessaryList.saveList, 'deleteUser', true).concat(getList(necessaryList.deleteList, 'deleteUser', false));

      //获取授权资源的savelist和deletelist
      function getList(arr = [], deleteUser, isnecessaary) {
        //获取对象数组
        return arr.map((item, index) => {
          let obj = {};
          obj.moduleId = me.moduleId;
          obj.resourceId = item;
          if (deleteUser !== undefined) {
            for (let i = 0; i < me.transfer.accredited.length; i++) {
              //获取对应的roleuserid
              if (me.transfer.accredited[i].id === obj.resourceId) {
                obj.id = me.transfer.accredited[i].moduleResourceId;
              }
            }
          }
          if (isnecessaary !== undefined) {
            //得到删除的必须资源
            obj.isNecessary = false;
          }
          return obj;
        });
      }

      //1 得到已授权资源的updateList的  necessaryList.saveList
      function getUpdateList(arr) {
        let tmp = [];
        arr.forEach(item => {
          for (let i = 0; i < me.transfer.accredited.length; i++) {
            //获取对应的roleuserid
            if (me.transfer.accredited[i].id === item) {
              let obj = {
                moduleId: me.moduleId,
                resourceId: item,
                id: me.transfer.accredited[i].moduleResourceId,
                isNecessary: true
              };
              tmp.push(obj);
            }
          }
        });
        return tmp;
      }

      let updateList = getUpdateList(necessaryList.saveList).concat(
          getList(necessaryList.deleteList, "deleteUser", false)
        ),
        saveList = getList(list.saveList);
      //2 新增的updatelist
      saveList.forEach(item => {
        necessaryList.saveList.forEach(val => {
          if (val == item.resourceId) {
            item.isNecessary = true;
          }
        });
      });

      this.$ajax
        .post("rest/module/moduleResource", {
          saveList, //getList(list.saveList),
          deleteList: getList(list.deleteList, "deleteUser")
          // updateList
        })
        .then(res => {
          if (res.data.status) {
            this.$message.success(res.data.answerMsg);
            this.sourceVisible = false;
          } else {
            this.$message.error(res.data.answerMsg);
          }
        });
    },
    handleRoleChange(val) {
      this.list.after = val.value; //保存的资源id
    },
    searchSourceModule() {
      this.query = deepCopy(this.searchSourceForm);
    },
    closeSourceModule() {
      this.searchSourceForm = {
        name: "",
        note: ""
      };
      this.necessaryList.before = [];
      this.necessaryList.after = [];
    },
    //自定义渲染穿梭框
    rendContent(h, option) {
      function rendInput() {
        if (option.isNecessary) {
          return (
            <input
              class="transfer-accredited el-checkbox__original"
              value={option.id}
              type="checkbox"
              checked="checked"
            />
          );
        }
        return (
          <input
            class="transfer-accredited el-checkbox__original"
            value={option.id}
            type="checkbox"
          />
        );
      }
      //           <label htmlFor="" class="module-label-source">
      //   {rendInput()}
      //   <span class="el-checkbox__inner" />
      // </label>
      return (
        <span>
          <span class="transfer-content">{option["name"]}</span>
        </span>
      );
    },
    sendBack() {
      this.moduleSortDialogVisible = false;
      this.searchModule();
    },
    //设置禁用后的class
    rowClassName(row, index) {
      if (!row.row.status) {
        return "disabled";
      }
      return "";
    }
  },
  created() {},
  mounted() {
    this.searchModule();
  },
  computed: {
    title() {
      let title = this.isAdd ? "新增模块" : "编辑模块";
      return title;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.module_div_search {
  display: flex;
  justify-content: space-between;
}

.module_div_operation {
  margin-bottom: 10px;
}

.module_div_operation i {
  font-size: 12px;
}

.module_div_operation i:first-of-type {
  font-size: 14px;
}

.module-div-sourceDialog {
  overflow: auto;
}

.module-div-sourceDialog > .el-dialog {
  width: 750px;
}

.module-div-sourceDialog .el-dialog__body {
  padding: 5px 15px 10px;
}

.module-div-addDialog .el-dialog__body {
  padding-right: 50px;
  max-height: 450px;
}

@media screen and (min-height: 820px) {
  .module-div-addDialog .el-dialog__body {
    max-height: none;
  }
}

.module-div-addDialog .el-cascader,
.module-div-addDialog .el-select {
  width: 100%;
}

.module-div-form .el-input {
  width: 100px;
}

/*资源*/
.source-table {
  margin-top: 10px;
}

.source-div-addDialog.el-dialog__wrapper {
  overflow: visible;
}

.source-div-addDialog > .el-dialog {
  width: 500px;
}

.source-div-addDialog > .el-dialog .el-dialog__body {
  padding-left: 45px;
}

.source-div-addDialog .el-form-item__label {
  min-width: 100px;
}

.source-div-addDialog .el-input__inner,
.source-div-addDialog .el-input {
  width: 200px;
}

.module-div-sourceDialog .el-transfer-panel:nth-child(3) .transfer-content {
  display: inline-block;
  max-width: 230px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.module-label-source {
  position: absolute;
  top: 3px;
  right: 16px;
  height: 14px;
  width: 14px;
}

.module-div-sourceDialog
  .el-transfer-panel:nth-of-type(1)
  .module-label-source {
  display: none;
}

/*.module-div-sourceDialog .el-transfer-panel:nth-child(3) {*/
/*width: 450px;*/
/*}*/

.transfer-accredited:checked + .el-checkbox__inner::after {
  transform: rotate(45deg) scaleY(1);
  border-color: #20a0ff;
}

.transfer-accredited:checked + .el-checkbox__inner {
  border-color: #20a0ff;
}

/*被禁用后的样式*/
.base-treeGrid .disabled {
  color: #919191;
}

.base-treeGrid .disabled .el-checkbox__inner {
  border: 1px solid #919191;
}

.base-treeGrid .disabled .el-button--warning,
.base-treeGrid .disabled .el-button--info {
  background: #eee;
  color: #919191;
  border-color: #919191;
}

/*是否必须资源*/
.module-source-isNecessary {
  position: fixed;
  right: -82px;
  top: 134px;
  z-index: 9999;
  background: #e4f5ff;
  color: #333;
  font-size: 12px;
  padding: 5px;
  border: 1px solid #67a7d3;
}

.module-source-isNecessary::after {
  display: inline-block;
  cursor: pointer;
  width: 0;
  height: 0;
  border: 6px solid transparent;
  border-top-width: 7px;
  border-top-color: #67a7d3;
  border-bottom-width: 0px;
  content: "";
  position: absolute;
  left: 10px;
  top: 24px;
}

.module-source-isNecessary strong {
  cursor: pointer;
  color: #007acf;
}

.el-transfer-panel__empty[style*="display: none;"]
  ~ .module-source-isNecessary {
  display: none;
}

.el-transfer-panel__body
  .el-transfer-panel__empty:nth-of-type(1)[style*="display: none;"]
  + .el-transfer-panel__empty[style*="display: none;"]
  + .module-source-isNecessary {
  display: block;
}
</style>


