<template>
  <div>
    <div class="wrap">
      <div class="left">
        <p>功能</p>
        <el-checkbox-group v-model="groupCheckList" @change="changeGroup">
          <ul>
            <li v-for="item in groupList" :key="item.nameCode" :class="{groupActive:selectGroup === item.nameCode}">
              <el-checkbox class="input_no_text" :label="item.nameCode"></el-checkbox>
              <el-button type="text" @click="selectGroupHandle(item.nameCode)">{{item.name}}</el-button>
            </li>
          </ul>
        </el-checkbox-group>
      </div>
      <div class="right">
        <p>资源</p>
        <el-checkbox-group v-if="detailList.length>0" v-model="detialCheckList" @change="changeRes">
          <ul>
            <li v-for="item in detailList" :key="item.name_code">
              <el-checkbox class="input_no_text" :label="item.id"></el-checkbox>
              <el-button type="text">{{item.name}}</el-button>
            </li>
          </ul>
        </el-checkbox-group>
        <p v-else>请添加资源</p>
      </div>
    </div>
    <small>*取消左侧功能将会导致资源被删除。</small>
  </div>
</template>
<script>
import { varType } from "@/util/util.js";
import { deepJSONCopy } from "../../util/util";
export default {
  props: {
    value: { type: Object },
    moduleId: { type: [String, Number] },
    initCheck: {
      // 初始化选中的数据
      type: Object
    },
    groupList: {
      type: Array
    },
    detailList: {
      type: Array
    }
  },
  data() {
    return {
      selectGroup: "", //this.groupList[0].nameCode
      groupCheckList: [],
      detialCheckList: [],
      groupDetialCheckList: {} // 当前所有功能选中的资源
      // init
    };
  },
  watch: {
    "initCheck.detailCheckedLists"(data) {
      if (data.length > 0) {
        let resouceCheck = {};
        // let allResouceCheck = {};
        data.forEach(item => {
          if (!resouceCheck[item.nameCode]) {
            resouceCheck[item.nameCode] = [];
            // allResouceCheck[item.nameCode] = [];
          }
          resouceCheck[item.nameCode].push(item.id);
          // allResouceCheck[item.nameCode].push(item);
        });
        this.groupDetialCheckList = resouceCheck;
        // this.groupInitCheck.groupCheckedAllResLists = allResouceCheck;
        if (this.selectGroup && this.groupDetialCheckList[this.selectGroup]) {
          this.detialCheckList = deepJSONCopy(
            this.groupDetialCheckList[this.selectGroup]
          );
        }
      } else {
        this.detialCheckList = [];
        this.groupDetialCheckList = {};
      }
    },
    "initCheck.groupCheckedLists"(data) {
      if (data.length > 0) {
        this.groupCheckList = data.map(item => {
          return item.nameCode;
        });
        this.selectGroup = this.groupCheckList[0];
        if (this.groupDetialCheckList[this.selectGroup]) {
          this.detialCheckList = deepJSONCopy(
            this.groupDetialCheckList[this.selectGroup]
          );
        }
      } else {
        this.groupCheckList = [];
        this.selectGroup = "";
        this.detialCheckList = [];
      }
    }
  },
  methods: {
    selectGroupHandle(code) {
      this.selectGroup = code;
      this.detialCheckList = deepJSONCopy(
        this.groupDetialCheckList[this.selectGroup] || []
      );
    },
    unique(arr1, arr2, callback) {
      let temp1 = {};
      let temp2 = {};
      let tempArr1 = [];
      let tempArr2 = [];
      for (let i = 0, len = arr1.length; i < len; i++) {
        temp1[arr1[i]] = true;
      }
      for (let i = 0, len = arr2.length; i < len; i++) {
        temp2[arr2[i]] = true;
      }

      for (let i = 0, len = arr1.length; i < len; i++) {
        if (!temp2[arr1[i]]) {
          tempArr1.push(callback(arr1[i], i, "del"));
        }
      }

      for (let i = 0, len = arr2.length; i < len; i++) {
        if (!temp1[arr2[i]]) {
          tempArr2.push(callback(arr2[i], i, "add"));
        }
      }
      return {
        del: tempArr1,
        add: tempArr2
      };
    },

    changeGroup(value) {
      let groupBalaLists = this.initCheck.groupCheckedLists.map(item => {
        return item.nameCode;
      });
      let result = this.unique(
        groupBalaLists,
        value,
        (element, index, status) => {
          let res = { moduleId: this.moduleId, nameCode: element };
          if (status === "del") {
            res.id = this.initCheck.groupCheckedLists[index].id;
          }
          return res;
        }
      );
      this.value.handleSaveList.handleList = result.add;
      this.value.handleDeleteList.handleList = result.del;
      this.$emit("input", this.value);
    },
    changeRes(value) {
      if (this.groupCheckList.indexOf(this.selectGroup) === -1) {
        this.groupCheckList.push(this.selectGroup);
        this.changeGroup(this.groupCheckList);
      }
      this.groupDetialCheckList[this.selectGroup] = value;
      let detailBalaLists = this.initCheck.detailCheckedLists.map(item => {
        return item.nameCode + "_" + item.id;
      });
      let newLists = [];
      for (let element in this.groupDetialCheckList) {
        let item = this.groupDetialCheckList[element];
        for (let i = 0, len = item.length; i < len; i++) {
          newLists.push(element + "_" + item[i]);
        }
      }
      let result = this.unique(
        detailBalaLists,
        newLists,
        (element, index, status) => {
          let item = element.split("_");
          let res = {
            resourceId: item[1],
            nameCode: item[0],
            moduleId: this.moduleId
          };
          if (status === "del") {
            res.id = this.initCheck.detailCheckedLists[
              index
            ].moduleHandleResourceId;
          }
          return res;
        }
      );
      this.value.handleSaveList.resourceList = result.add;
      this.value.handleDeleteList.resourceList = result.del;
      this.$emit("input", this.value);
    }
  }
};
</script>
<style scoped>
.wrap {
  display: flex;
}
.left,
.right {
  flex: 1;
}
.groupActive {
  background-color: #e1ffb9;
}
</style>



